<template>
  <div :class="['group_check', 'type_box',checkListClass]" ref="groupCheck">
    <CheckboxItem v-for="(item, index) in dataList"
      :key="index"
      :id="item.id"
      :text="item.text"
      :style="itemStyle"
      :isDisabled="isDisabled || item.isDisabled" 
      v-model:checkedNames="checkedNamesSync" />
  </div>
</template>

<script>
import CheckboxItem from './CheckboxItem'

import { mapState } from 'vuex';

export default {
  name: 'Checkbox',
  components: {
    CheckboxItem
  },
  props: {
    dataList: Array,
    checkedNames:Array,
    isDisabled: {
      type: Boolean,
      default: false
    },
    itemStyle: String,
  },
  mounted(){
    this.setRowHeight();
  },
  watch:{
    windowWidth(){
      this.setRowHeight();
    }
  },
  computed: {
    checkedNamesSync: {
      get() {
        return this.checkedNames;
      },
      set(value) {
        this.$emit("update:checkedNames", value);
      }
    },
    checkListClass(){
      if( !this.dataList )  return '';

      var lengthValue = this.dataList.length;
      if(lengthValue >= 5){
        lengthValue = '_multi';
      }
      if(this.windowWidth <= 480 && lengthValue == 4){
        lengthValue = 2;
      }
      return 'list_check' + lengthValue;
    },
    ...mapState({
      windowWidth: state => state.viewport.windowWidth,
    }),
  },
  methods:{
    setRowHeight(){
      const childrenList = this.$refs.groupCheck.children;
      if(childrenList && childrenList.length > 0){
        const childrenHeightList = Array.from(childrenList).map(item => item.children[1].children[0].offsetHeight);
        const childrenTopList = Array.from(childrenList).map(item => item.offsetTop);
        var targetIndexList = [];
        var maxHeightList = [];
        childrenHeightList.forEach((item, index) => {
          targetIndexList.push(index);
          maxHeightList.push(item);
          const isChangedRow = childrenTopList[index] != childrenTopList[index+1];
          if(isChangedRow){
            var maxHeight = Math.max(...maxHeightList);
            targetIndexList.forEach(targetIndex => {
              childrenList[targetIndex].children[1].style.height = maxHeight+'px';
            })
            targetIndexList = [];
            maxHeightList = [];
          }
        });
      }
    },
  },
}
</script>
<style scoped>
.type_box{overflow:hidden;padding-bottom:1px}

.type_box .check_comm{float:left}
.type_box .check_comm:deep() label{padding:0 26px 0 13px;border:1px solid #e1e1e1;background-color:#fff;font-size:0;white-space:nowrap;background-color:#fbfbfb}
.type_box .check_comm:deep() label:after{display:inline-block;height:100%;vertical-align:middle;content:''}
.type_box .check_comm:deep() .txt_checkbox{display:inline-block;padding:9px 0;vertical-align:middle;white-space:normal}
.type_box .check_comm:deep() .ico_checkbox{top:0;left:inherit;right:10px}

.type_box .check_comm:deep() input:checked + label:before{position:absolute;top:-1px;bottom:-1px;left:-1px;right:-1px;border:1px solid #222;content:''}
.type_box .check_comm:deep() input:checked + label{position:relative;z-index:1;font-family:'NotoSansKR_M', sans-serif;background-color:#fff;color:#222}

.type_box .check_comm:hover:deep() label{color:#222}
.type_box .check_comm + .check_comm{margin-left:-1px}

.type_box.list_check2 .check_comm{width:50%}
.type_box.list_check2 .check_comm:nth-child(2n+1){clear:left}
.type_box.list_check2 .check_comm:nth-child(2n+3){margin-left:0}
.type_box.list_check2 .check_comm:nth-child(2n+3):deep() label{border-left:1px solid #e1e1e1}
.type_box.list_check2 .check_comm:nth-child(2n+2):deep() label{margin-left:-1px}
.type_box.list_check2 .check_comm:nth-child(n+3):deep() label{margin-top:-1px}
.type_box.list_check3 .check_comm{width:33.333%}
.type_box.list_check4 .check_comm{width:25%}
.type_box.list_check_multi .check_comm + .check_comm{margin-left:auto}
.type_box.list_check_multi .check_comm{width:33.333%}
.type_box.list_check_multi .check_comm:nth-child(3n+1){clear:left}
.type_box.list_check_multi .check_comm:nth-child(n+4){margin-top:-1px}
.type_box.list_check_multi .check_comm + .check_comm:nth-child(3n+2):deep() label,
.type_box.list_check_multi .check_comm + .check_comm:nth-child(3n):deep() label{margin-left:-1px}

.util_map .type_box .check_comm{width:auto}
.util_map .type_box .check_comm:deep() label{padding:8px 22px 8px 8px}
.util_map .type_box .check_comm:deep() .ico_checkbox{top:0;left:inherit;right:6px}
.util_map .type_box .check_comm:deep() input:checked + label{padding:7px 22px 8px 7px}
.util_map .type_box .check_comm:deep() input:checked + label + .ico_checkbox{right:5px}
</style>
