<template>
  <InputBase :class="{ disabled: isDisabled, tf_error: isError, tf_value: value != '' }">
    <input 
      :inputmode="inputmode"
      v-model="input" 
      :placeholder="placeholder" 
      :disabled="isDisabled"
      @focus="onFocus"
      @blur="onBlur"
      ref="input"
      KeyboardEvent.key:keyup.13="onKeyUpEnter" />
  </InputBase>
</template>

<script>
import InputBase from './InputBase'
import { getCanConvertNumber } from '@/utils/numberUtils'
import { addCommaForMoney } from '@/utils/stringUtils'

export default {
  name: 'InputNumber',
  components: {
    InputBase,
  },
  props: {
    value: String | Number,
    placeholder: String,
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxLength: Number,
    inputmode: {
      type: String,
      default: 'numeric',
    },
  },
  computed: {
    input: {
      get() {
        const validText = this.getValidText( this.value );

        if( validText !== this.value ) {
          this.$emit("update:value", validText );
        }
        
        if( this.maxLength && this.value.length > this.maxLength ) {
          this.$emit("update:value", this.value.slice( 0, this.maxLength ));
        }
        return this.value;
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  methods: {
    getValidText( strOrg ) {
      if( !strOrg || strOrg == null || strOrg == '' ) return '';
      strOrg = strOrg.trim();
      // 그냥 다 숫자면 그대로 리턴.
      if( getCanConvertNumber( strOrg ) ) {
        return addCommaForMoney( strOrg );
      }
      let ret = '';
      let char;
      for(var i=0; i<strOrg.length; ++i) {
        char = strOrg.charAt( i );
        if( !getCanConvertNumber( char ) )  continue;
        ret += char;
      }
      if( !ret )  return '';
      const nRet = Number.parseInt( ret, 10 );
      return addCommaForMoney( nRet );
    },
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter() {
      this.blur();
    }
  }
}
</script>