<template>
  <div class="group_select_number">
    <button type="button" class="btn_number btn_subtract" @click="onClickControal(false)">-</button>
    <InputNumber 
      :class="['tf_border',{ disabled: isDisabled, tf_error: isError }]"
      v-model:value="input"
      :placeholder="placeholder"
      :disabled="isDisabled"
      @focus="onFocus"
      @blur="onBlur"
      @input="onInput" />
    <button
      type="button"
      class="btn_number btn_add"
      :disabled="Number(value) == maxValue"
      @click="onClickControal(true)">
      +
    </button>
  </div>
</template>

<script>
import InputBase from '@/components/common/input/InputBase';
import InputNumber from '@/components/common/input/InputNumber';

export default {
  name: 'InputSelectNumber',
  components: {
    InputBase,
    InputNumber
  },
  props: {
    value: Number | String,
    placeholder: {
      type: String,
      default: null
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxValue: Number,
    isFocusOnMount: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    input: {
      get() {
        if( this.maxValue && Number(this.value) > this.maxValue ){
            this.$emit("onChange", String(this.maxValue) );
            this.$emit("update:value", String(this.maxValue) );
        }

        if( typeof(this.value) == 'number' ){
            this.$emit("onChange", String(this.value) );
            this.$emit("update:value", String(this.value) );
        }
        if(!this.value) return '';
        return String(this.value);
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  mounted() {
    if( this.isFocusOnMount ) {
      this.$refs.input.focus();
    }
  },
  methods: {
    onClickControal( isAdd ){
      const numberVal = Number(this.value);
      if(numberVal == 0 && !isAdd) return;
      const changedVal = numberVal + (1 * isAdd ? +1 : -1);
      this.$emit("update:value", changedVal);
    },
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter( e ) {
      this.blur();

      this.$emit("onEnterKey", e);
    },
    onInput(e) {
      this.$emit("onInput", e);
    }
  }
}
</script>
<style scoped>
.group_select_number .btn_number{display:inline-block;width:24px;height:24px;border-radius:24px;border:1px solid #e1e1e1;vertical-align:top}
.group_select_number .btn_number:disabled{background-color:#f9f9f9;cursor:default}
.btn_subtract{margin:6px 6px 0 0}
.btn_add{margin:6px 0 0 6px}
.group_select_number .tf_comm{width:60px}
.group_select_number .tf_comm:deep() input{text-align:center}


/* 모바일 */
