<template>
  <div :class="['section_sticky', { sticky: isSticky }]" ref="sectionSticky">
    <div class="box_sticky" :style="boxCssProps">
      <div class="inner_sticky" ref="innerSticky" :style="innerCssProps">
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: "Sticky",
  mounted() {
    this.sectionSticky = this.$refs.sectionSticky;
    this.bindEventSticky();
    window.addEventListener("resize", this.onResize);
  },
  beforeUnmoun() {
    window.removeEventListener("resize", this.onResize);
    document
      .getElementById("cMain")
      .removeEventListener("scroll", this.stopSticky);
  },
  updated() {
    // when data changes -> virtual DOM re-render and patch.
    // https://kr.vuejs.org/v2/guide/instance.html#라이프사이클-다이어그램
    this.bindEventSticky();
  },
  data() {
    return {
      isSticky: false,
      sectionSticky: undefined,
    };
  },
  computed:{
    ...mapState({
      isActiveGnb: state => state.gnb.isActiveGnb,
    }),
    boxCssProps(){
      const obj = {
        '--left': this.isActiveGnb ? '180px' : '60px',
      }
      return obj;
    },
    innerCssProps(){
      const obj = {
        '--maxWitdh': this.sectionSticky ? this.sectionSticky.offsetWidth+'px' :  'auto',
      }
      return obj;
    },
  },
  methods: {
    bindEventSticky() {
      if (this.sectionSticky.length === 0) return false;
      this.stopSticky();
      window.addEventListener("scroll", this.stopSticky);
    },
    stopSticky() {
      this.$nextTick(() => {
        var windowHeight = window.innerHeight;
        var windowTop = window.scrollY;

        var sectionStickyHeight = this.sectionSticky.offsetHeight;
        var sectionStickyTop = this.sectionSticky.offsetTop;

        var stopPoint = sectionStickyTop + sectionStickyHeight - windowHeight;
        this.isSticky = windowTop < stopPoint ? true : false;
      });
    },
    onResize() {
      this.bindEventSticky();
    }
  }
};
</script>

<style scoped>
.section_sticky{height:72px;margin-top:80px}
.section_sticky .box_sticky{position:static;bottom:0;left:0;right:0;z-index:100;background-color:#fff;text-align:center}
.section_sticky .inner_sticky{overflow:hidden;margin:0 auto;padding:16px 40px;font-size:0;box-sizing:border-box}
.section_sticky:deep() button{width:120px}
.section_sticky:deep() button + button{margin:0 0 0 8px}

.section_sticky.sticky .box_sticky{position:fixed;left:var(--left);right:0;box-shadow:0 -2px 8px 0 rgba(34, 34, 34, 0.15)}
.section_sticky.sticky .inner_sticky{max-width:var(--maxWitdh)}

/* 모바일 */
@media all and (max-width:800px){
  .section_sticky .inner_sticky{padding:16px 20px}
}
/* 모바일 */
@media all and (max-width:600px){
  .section_sticky{height:72px;margin-top:40px}
  .section_sticky.sticky .box_sticky{left:0}
  .section_sticky:deep() .area_right:only-child{width:100%}
  .section_sticky:deep() .area_right:only-child button:only-child{width:100%}
}
/* 모바일 */
@media all and (max-width:500px){
  .section_sticky.sticky .box_sticky{left:0}
}
</style>
