<template>
  <div class="group_tf_unit">
    <InputBase :class="{ disabled: isDisabled, tf_error: isError }">
      <input 
        v-model="input" 
        :type="type" 
        :placeholder="placeholder" 
        :maxLength="maxLength"
        :disabled="isDisabled"
        ref="input"
        KeyboardEvent.key:keyup.13="onKeyUpEnter"
        @focus="onFocus"
        @blur="onBlur"
        @input="onInput" />
    </InputBase>
    <span class="txt_unit">{{ unit }}</span>
  </div>
</template>

<script>
import InputBase from '@/components/common/input/InputBase';
import { getCanConvertNumber } from '@/utils/numberUtils'
import { addCommaForMoney } from '@/utils/stringUtils'
import { changeOnlyKorEng , changeOnlyKorEngSpace } from '@/utils/stringUtils';

export default {
  name: 'InputUnit',
  components: {
    InputBase
  },
  props: {
    value: String | Number,
    type: {
      type: String,
      default: 'text'
    },
    unit:{
      type: String,
      default: '개'
    },
    placeholder: {
      type: String,
      default: null
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxLength: Number,
    isFocusOnMount: {
      type: Boolean,
      default: false
    },
    isOnlyEngKor: {
      type: Boolean,
      default: false,
    },
    isOnlyEngKorSpace : {
      type: Boolean,
      defualt: false,
    }
  },
  computed: {
    input: {
      get() {
        const validText = this.getValidText( this.value );

        if( validText !== this.value ) {
          this.$emit("update:value", validText );
        }

        if (this.isOnlyEngKor) {
          const validText = changeOnlyKorEng( this.value );

          if( validText !== this.value ) {
            this.$emit("onChange", validText );

            this.$emit("update:value", validText );
          }
        }

        if(this.isOnlyEngKorSpace) {
          const validText = changeOnlyKorEngSpace( this.value );

          if( validText !== this.value ) {
            this.$emit("onChange", validText );

            this.$emit("update:value", validText );
          }
        }

        if( this.maxLength && this.value.length > this.maxLength ) {
          this.$emit("onChange", this.value.slice( 0, this.maxLength ));

          this.$emit("update:value", this.value.slice( 0, this.maxLength ));
        }

        return this.value;
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  mounted() {
    if( this.isFocusOnMount ) {
      this.$refs.input.focus();
    }
  },
  methods: {
    getValidText( strOrg ) {
      if( !strOrg ) return '';
      strOrg = strOrg.trim();
      // 그냥 다 숫자면 그대로 리턴.
      if( getCanConvertNumber( strOrg ) ) {
        return addCommaForMoney( strOrg );
      }
      let ret = '';
      let char;
      for(var i=0; i<strOrg.length; ++i) {
        char = strOrg.charAt( i );
        if( !getCanConvertNumber( char ) )  continue;
        ret += char;
      }
      if( ret ){
        const nRet = Number.parseInt( ret, 10 );
        return addCommaForMoney( nRet );
      }else{
        return strOrg;
      }
    },
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter( e ) {
      this.blur();

      this.$emit("onEnterKey", e);
    },
    onInput(e) {
      this.$emit("onInput", e);
    }
  }
}
</script>
<style scoped>
.group_tf_unit{overflow:hidden}
.group_tf_unit .tf_comm{float:left;width:calc(100% - 32px);max-width:200px}
.group_tf_unit .tf_comm input{text-align:right}
.txt_unit{display:inline-block;overflow:hidden;margin:12px 0 0 8px;font-size:12px;line-height:16px;vertical-align:top}

/* 모바일 */
@media all and (max-width:435px){
  .group_tf_unit .tf_comm{max-width:inherit}
}
</style>